
.call-to-action {
  background: #ff8b6a;
  color: whie;
  padding: 4px 10px;
  border-radius: 8px;
  font-weight: 200;
  text-transform: uppercase;
}
.header .call-to-action {
  color: #fff;
}
.button-submit {
  background: #856aff;
  color: #fff;
  padding: 10px 20px;
  cursor: pointer;
}
.button-submit-small {
  background: #856aff;
  color: #fff;
  padding: 5px 10px;
  border-radius: 2px;
  cursor: pointer;
}
.button-delete {
  background: #fbf4f4;
  color: #000;
  padding: 5px 10px;
  cursor: pointer;
}
.section-title {
  font-weight: 300;
}
.col-centered {
  float: none;
  margin: 0 auto;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  font-size: 14px;
  background-color: #fff;
  margin: 0;
}
body #app {
  margin-bottom: 120px;
}
a {
  color: #34495e;
  text-decoration: none;
}
.header {
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
}
.header .inner {
  max-width: 800px;
  box-sizing: border-box;
  margin: 0px auto;
  padding: 15px 5px;
}
.header a {
  font-size: 14px;
  color: #856aff;
  line-height: 24px;
  transition: color 0.15s ease;
  display: inline-block;
  vertical-align: middle;
  font-weight: 300;
  letter-spacing: 0.075em;
  margin-right: 1.8em;
}
.header a:hover {
  color: #ff8b6a;
  text-decoration: none;
}
.header a.router-link-active {
  color: #856aff;
  text-decoration: underline;
}
.header a:nth-child(6) {
  margin-right: 0;
}
.header .github {
  color: #fff;
  font-size: 0.9em;
  margin: 0;
  float: right;
}
.player-holder {
  padding: 10px 20px;
  box-shadow: -5px -5px 5px -2px rgba(176,176,176,0.3);
  background-color: #fff;
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
}
.logo {
  width: 30px;
  margin-right: 10px;
  display: inline-block;
  vertical-align: middle;
}
.feed-icon {
  width: 30px;
}
.registration-icon {
  width: 30px;
}
.like-icon {
  width: 30px;
}
.trophy-icon {
  width: 30px;
}
.new-icon {
  width: 30px;
}
.view {
  max-width: 800px;
  margin: 0 auto;
  position: relative;
}
.fade-enter-active,
.fade-leave-active {
  transition: all 0.2s ease;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
@media (max-width: 860px) {
.header .inner {
    padding: 15px 30px;
}
}
@media (max-width: 600px) {
body {
    font-size: 14px;
}
.header {
    min-height: 80px;
}
.header .inner {
    padding: 15px;
}
.header a {
    margin-right: 1em;
}
.header .github {
    display: none;
}
}


